'use client';

import React, { useState, useTransition, useRef } from 'react';
import Link from 'next/link';
import { 
  Sparkles, 
  Brain, 
  Wand2, 
  ImageIcon, 
  Mic, 
  FileEdit, 
  CheckSquare, 
  Lightbulb, 
  ChevronLeft, 
  Bookmark, 
  Tag, 
  BarChart3
} from 'lucide-react';

import { extractQuestionFromText } from '../actions'; // Import Server Action

const CreateQuestionWithAIPage: React.FC = () => {
  // Refs để truy cập trực tiếp các file input
  const imageInputRef = useRef<HTMLInputElement>(null);
  const audioInputRef = useRef<HTMLInputElement>(null);

  // State cho trạng thái loading của action
  const [isExtracting, startTransition] = useTransition();

  // State cho các trường của câu hỏi (sẽ được AI điền vào)
  const [rawInput, setRawInput] = useState('');
  const [questionText, setQuestionText] = useState('');
  const [options, setOptions] = useState<string[]>(['', '', '', '']);
  const [correctIndex, setCorrectIndex] = useState<number | null>(null);
  const [explanation, setExplanation] = useState('');
  const [difficulty, setDifficulty] = useState('');
  const [topic, setTopic] = useState('');
  const [tags, setTags] = useState<string[]>([]);
  const [formError, setFormError] = useState<string | null>(null);

  const handleFormAction = (formData: FormData) => {
    // Kiểm tra xem có văn bản không
    if (!formData.get('rawInput')?.toString().trim()) {
        setFormError("Vui lòng nhập nội dung câu hỏi thô.");
        return;
    }
    setFormError(null);

    startTransition(async () => {
      const result = await extractQuestionFromText(formData);

      if (result.success && result.data) {
        const { data } = result;
        // Cập nhật state với dữ liệu từ AI
        setQuestionText(data.questionText || '');
        setOptions(data.options || ['', '', '', '']);
        setCorrectIndex(data.correctOptionIndex !== undefined ? data.correctOptionIndex : null);
        setExplanation(data.explanation || '');
        setDifficulty(data.difficulty || '');
        setTopic(data.topic || '');
        setTags(data.tags || []);
      } else {
        // Hiển thị thông báo lỗi cho người dùng
        setFormError(result.error || 'Đã có lỗi không xác định xảy ra.');
      }
    });
  };

  const updateOption = (idx: number, val: string) => {
    const newOpts = [...options];
    newOpts[idx] = val;
    setOptions(newOpts);
  };
  
  // Nút Lưu tạm thời
  const handleSave = () => {
    console.log("Đã nhấn Lưu:", { questionText, options, correctIndex, explanation, difficulty, topic, tags });
    alert("Câu hỏi đã được lưu thành công vào ngân hàng! ( giả lập ) ");
  };

  return (
    // Thẻ <form> bao bọc toàn bộ, sử dụng action của server
    <form action={handleFormAction} className="max-w-7xl mx-auto w-full px-4 md:px-6 lg:px-8 py-8">
      
      {/* === PHẦN HEADER CỦA TRANG === */}
      <div className="mb-8">
        <Link href="/dashboard/teacher/questions" className="flex items-center gap-2 text-sm text-gray-500 hover:text-[#2b8cee] transition-colors mb-3 w-fit">
            <ChevronLeft size={18} />
            Quay lại danh sách câu hỏi
        </Link>
        <div className="flex flex-col md:flex-row md:items-end justify-between gap-4">
            <div className="flex flex-col gap-2">
              <div className="flex items-center gap-2">
                <span className="px-2.5 py-1 rounded-md bg-[#2b8cee]/10 text-[#2b8cee] text-xs font-bold uppercase tracking-wider">AI POWERED</span>
                <h1 className="text-3xl lg:text-4xl font-black text-gray-900 leading-tight tracking-tight">Tạo Câu Hỏi Thông Minh</h1>
              </div>
              <p className="text-gray-500 text-base">Dán văn bản, tải tệp lên và để AI tự động phân tích và tạo câu hỏi.</p>
            </div>
            <div className="flex items-center gap-3">
                <button type="button" onClick={() => window.location.href='/dashboard/teacher/questions'} className="h-11 px-6 rounded-xl font-bold text-sm bg-white border border-gray-200 text-gray-800 hover:bg-gray-50 transition-all">
                    Hủy bỏ
                </button>
                <button type="button" onClick={handleSave} className="h-11 px-8 rounded-xl font-bold text-sm bg-[#09090b] text-white hover:bg-[#27272a] transition-all">
                    Lưu câu hỏi
                </button>
            </div>
        </div>
      </div>

      {/* Layout 2 cột: Cột chính (7 phần) và Cột Phân loại (3 phần) */}
      <div className="grid grid-cols-1 lg:grid-cols-10 gap-8 items-start">
        
        {/* === CỘT CHÍNH (NỘI DUNG) === */}
        <div className="lg:col-span-7 flex flex-col gap-8">
          {/* --- VÙNG NHẬP LIỆU AI --- */}
          <section className="w-full">
            <div className="bg-white rounded-2xl p-8 shadow-lg border-2 border-[#2b8cee]/20 relative overflow-hidden transition-all duration-300">
              <div className="absolute -top-10 -right-10 opacity-5 pointer-events-none">
                <Sparkles size={200} className="text-[#2b8cee]" />
              </div>
              <div className="relative z-10 flex flex-col gap-6">
                <div className="flex items-center gap-4">
                  <div className="p-3 rounded-lg bg-gradient-to-br from-[#2b8cee] to-blue-400 text-white shadow-md"><Brain size={28} /></div>
                  <div>
                    <h2 className="text-xl font-bold text-gray-900">1. Cung cấp dữ liệu</h2>
                    <p className="text-sm text-gray-500">Dán văn bản và tùy chọn đính kèm tệp âm thanh hoặc hình ảnh.</p>
                  </div>
                </div>
                
                <textarea 
                  name="rawInput" // Thêm name để FormData có thể lấy giá trị
                  value={rawInput}
                  onChange={(e) => setRawInput(e.target.value)}
                  className="w-full rounded-xl border border-gray-200 bg-gray-50 focus:border-[#2b8cee] focus:ring-2 focus:ring-[#2b8cee]/20 min-h-[160px] p-6 text-base placeholder:text-gray-400 transition-all"
                  placeholder="Ví dụ: Question: What is the capital of France? A. London B. Paris C. Rome D. Berlin. Correct: B..."
                ></textarea>
                
                <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
                  <div className="group flex flex-col items-center justify-center gap-3 rounded-lg border-2 border-dashed border-gray-200 p-6 transition-all bg-gray-50/50">
                    <div className="size-12 rounded-full bg-[#2b8cee]/10 flex items-center justify-center text-[#2b8cee]"><ImageIcon size={24} /></div>
                    <input ref={imageInputRef} name="imageFile" accept="image/*" type="file" className="text-sm text-gray-600 file:mr-4 file:py-2 file:px-4 file:rounded-full file:border-0 file:text-sm file:font-semibold file:bg-[#2b8cee]/10 file:text-[#2b8cee] hover:file:bg-[#2b8cee]/20 cursor-pointer"/>
                  </div>
                  <div className="group flex flex-col items-center justify-center gap-3 rounded-lg border-2 border-dashed border-gray-200 p-6 transition-all bg-gray-50/50">
                    <div className="size-12 rounded-full bg-[#2b8cee]/10 flex items-center justify-center text-[#2b8cee]"><Mic size={24} /></div>
                    <input ref={audioInputRef} name="audioFile" accept="audio/*" type="file" className="text-sm text-gray-600 file:mr-4 file:py-2 file:px-4 file:rounded-full file:border-0 file:text-sm file:font-semibold file:bg-[#2b8cee]/10 file:text-[#2b8cee] hover:file:bg-[#2b8cee]/20 cursor-pointer"/>
                  </div>
                </div>

                <div className="flex flex-col items-center gap-4">
                  <button 
                    type="submit" // Quan trọng: type="submit" để kích hoạt form action
                    disabled={isExtracting}
                    className="w-full md:w-auto bg-gradient-to-br from-[#2b8cee] to-blue-400 hover:scale-[1.02] active:scale-[0.98] text-white px-10 py-4 rounded-xl font-bold text-base flex items-center justify-center gap-3 transition-all shadow-lg shadow-[#2b8cee]/30 disabled:opacity-60 disabled:cursor-not-allowed disabled:hover:scale-100"
                  >
                    <Wand2 size={20} className={isExtracting ? 'animate-spin' : ''} />
                    {isExtracting ? 'AI đang phân tích...' : 'Trích xuất với AI'}
                  </button>
                  {formError && <p className="text-sm text-red-600">{formError}</p>}
                </div>
              </div>
            </div>
          </section>

          {/* --- CÁC TRƯỜNG KẾT QUẢ DO AI ĐIỀN --- */}
          <div className="bg-white rounded-xl p-6 shadow-sm border border-gray-200 flex flex-col gap-4">
            <label className="text-base font-bold flex items-center gap-3 text-gray-800">
              <FileEdit size={20} className="text-[#2b8cee]" />
              Nội dung câu hỏi (Kết quả từ AI)
            </label>
            <textarea 
              value={questionText}
              onChange={(e) => setQuestionText(e.target.value)}
              className="w-full resize-none rounded-lg border border-gray-200 bg-gray-50 focus:border-[#2b8cee] focus:ring-2 focus:ring-[#2b8cee]/20 min-h-[120px] p-4 text-base transition-all"
              placeholder="AI sẽ điền nội dung câu hỏi đã được chuẩn hóa vào đây..."
            ></textarea>
          </div>

          <div className="bg-white rounded-xl p-6 shadow-sm border border-gray-200 flex flex-col gap-5">
            <label className="text-base font-bold flex items-center gap-3 text-gray-800">
              <CheckSquare size={20} className="text-[#2b8cee]" />
              Các phương án trả lời (Kết quả từ AI)
            </label>
            <div className="flex flex-col gap-4">
              {options.map((opt, i) => (
                <div key={i} className="flex items-center gap-4">
                  <input 
                    checked={correctIndex === i}
                    onChange={() => setCorrectIndex(i)}
                    className="size-5 border-2 border-gray-300 text-[#2b8cee] focus:ring-[#2b8cee]/50 cursor-pointer transition-all shrink-0 mt-0.5"
                    name="correct_answer" 
                    type="radio"
                  />
                  <div className="flex-1">
                    <input 
                      value={opt}
                      onChange={(e) => updateOption(i, e.target.value)}
                      className={`w-full rounded-lg border p-3.5 text-base transition-all ${correctIndex === i ? 'border-[#2b8cee] bg-[#2b8cee]/5 font-semibold shadow-inner' : 'border-gray-200 bg-gray-50'}`}
                      placeholder={`Đáp án ${String.fromCharCode(65 + i)}...`}
                      type="text" 
                    />
                  </div>
                </div>
              ))}
            </div>
          </div>

          <div className="bg-white rounded-xl p-6 shadow-sm border border-gray-200 flex flex-col gap-4">
            <label className="text-base font-bold flex items-center gap-3 text-gray-800">
              <Lightbulb size={20} className="text-[#2b8cee]" />
              Giải thích chi tiết (Kết quả từ AI)
            </label>
            <textarea 
              value={explanation}
              onChange={(e) => setExplanation(e.target.value)}
              className="w-full resize-none rounded-lg border border-gray-200 bg-gray-50 focus:border-[#2b8cee] focus:ring-2 focus:ring-[#2b8cee]/20 min-h-[140px] p-4 text-base transition-all"
              placeholder="AI sẽ điền lời giải thích chi tiết cho đáp án đúng vào đây..."
            ></textarea>
          </div>
        </div>

        {/* === CỘT PHỤ (PHÂN LOẠI) === */}
        <div className="lg:col-span-3 flex flex-col gap-6 lg:sticky lg:top-8">
            <div className="bg-white rounded-xl p-6 shadow-sm border border-gray-200 flex flex-col gap-5">
                <h3 className="text-base font-bold flex items-center gap-3 text-gray-800">
                    <Brain size={20} className="text-[#2b8cee]"/>
                    2. Phân loại bởi AI
                </h3>
                
                {/* Hiển thị Độ khó */}
                <div className="flex flex-col gap-2">
                    <label className="text-sm font-semibold text-gray-600 flex items-center gap-2"><BarChart3 size={16}/>Độ khó</label>
                    <div className="px-4 py-2 bg-gray-100 text-gray-800 rounded-lg text-sm font-medium">
                        {difficulty || 'Chờ AI phân tích...'}
                    </div>
                </div>
                
                {/* Hiển thị Chủ đề */}
                <div className="flex flex-col gap-2">
                    <label className="text-sm font-semibold text-gray-600 flex items-center gap-2"><Bookmark size={16}/>Chủ đề</label>
                    <div className="px-4 py-2 bg-gray-100 text-gray-800 rounded-lg text-sm font-medium">
                        {topic || 'Chờ AI phân tích...'}
                    </div>
                </div>

                {/* Hiển thị Tags */}
                <div className="flex flex-col gap-2">
                    <label className="text-sm font-semibold text-gray-600 flex items-center gap-2"><Tag size={16}/>Tags (Từ khóa)</label>
                    <div className="flex flex-wrap gap-2 p-2 bg-gray-100 rounded-lg min-h-[40px]">
                        {tags.length > 0 ? (
                            tags.map(tag => (
                                <span key={tag} className="px-2.5 py-1 text-xs font-semibold text-white bg-[#2b8cee] rounded-full">
                                    {tag}
                                </span>
                            ))
                        ) : (
                            <span className="text-sm text-gray-500 px-2">Chờ AI phân tích...</span>
                        )}
                    </div>
                </div>
            </div>
        </div>
      </div>
    </form>
  );
};

export default CreateQuestionWithAIPage;
